AAEV,0BAHF,kBAII,UAAW,CACX,kBAAmB,CAEtB;AAED,oBACE,SAAU;AAEV,0BAHF,oBAII,UAAW,CAEd;AAED,sDAEE,YAAa;AACd,0BAED,mBAEI,kBAAmB,CAEtB;AAED,4BACE,6BAA8B,CAC9B,kBAAmB;AAEnB,0BAJF,4BAKI,qBAAsB,CAEzB;AAED,iBACE,SAAU,CACV,iBAAkB;AACnB,iBAGC,kBAAmB;AAEnB,0BAHF,iBAII,SAAU,CACV,eAAgB,CAChB,kBAAmB,CAMtB;AAHC,0BATF,iBAUI,UAAW,CAEd;AAGH,cACE,uBAAwB,CACxB,oBAAqB,CACrB,eAAgB,CAChB,cAAe,CACf,QAAS,CACT,yBAA0B,CAC1B,eAAgB,CAChB,cAAe,CACf,kBAAmB,CACnB,WAAY;AACb,iBAGC,SAAU,CACV,qBAAsB,CACtB,mBAAoB,CACpB,iBAAkB,CAClB,OAAQ,CACR,OAAQ,CACR,0BAA2B,CAC3B,WAAY","file":"above_fold.scss","sourcesContent":["body {\n  margin: 0;\n  font-family: 'Raleway', sans-serif;\n  color: $font-colour-primary;\n}\n\n*,\n*:before,\n*:after {\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  box-sizing: border-box;\n}\n\ninput,\nselect {\n\n  &:focus {\n    outline: 0;\n  }\n}\n\nbutton:focus {\n  outline: 0;\n}\n\nselect {\n  background: white;\n}\n\n.container {\n  max-width: 1140px;\n  margin: 0 auto;\n  padding: 0 15px;\n}\n\n.row {\n  display: flex;\n  flex-wrap: wrap;\n  margin: 0 -15px;\n}\n\nul {\n  list-style-type: none;\n  padding-left: 0;\n  margin: 0;\n}\n\na {\n  text-decoration: none;\n\n  &:visited {\n    text-decoration: none;\n  }\n\n  &:hover {\n    text-decoration: underline;\n  }\n}\n\nhr {\n  border: 0;\n  background-color: $border-colour;\n  margin: 0;\n  height: 1px;\n}\n\n.adsbygoogle {\n  overflow: hidden;\n}\n\n.gradient {\n  background: linear-gradient(45deg, $green, $light-green);\n}\n\n.star {\n  width: 13px;\n  color: gold;\n}\n\nh1,\nh2 {\n  margin: 0;\n}","@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500&display=swap');\n\n$input-font-size: 20px;\n$font-colour-primary: #5f5f5f;\n$green: #00be34;\n$light-green: #00ff47;\n$border-colour: #eaeaea;\n$footer-background-colour: #f1f1f1;\n$button-hover: #2de25e;\n$button-active: #72f195;\n$breadcrumb-colour: #f5f5f5;\n$logo-width: 150px;\n","@keyframes placeHolderShimmer {\n    0% {\n        background-position: -468px 0\n    }\n\n    100% {\n        background-position: 468px 0\n    }\n}\n\n.animated-background {\n    animation-duration: 1s;\n    animation-fill-mode: forwards;\n    animation-iteration-count: infinite;\n    animation-name: placeHolderShimmer;\n    animation-timing-function: linear;\n    background: #f6f7f8;\n    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);\n    background-size: 800px 104px;\n    height: 10px;\n    width: 60px;\n    position: relative;\n}",".breadcrumb {\n  background-color: $breadcrumb-colour;\n  display: inline-block;\n  padding: 8px 15px;\n\n  &__close {\n    color: $font-colour-primary;\n    width: 7px;\n    vertical-align: middle;\n  }\n\n  &__text {\n    margin-right: 5px;\n  }\n}\n",".categories-bar {\n  padding: 15px 0;\n\n  &--one-line {\n    overflow-x: scroll;\n    white-space: nowrap;\n  }\n\n  &--shorter {\n    padding: 7px 0;\n  }\n\n  &__list-item {\n    margin-right: 5px;\n    display: inline-block;\n  }\n\n  &__category {\n    display: inline-block;\n    color: $green;\n    padding: 5px;\n    transition: background-color 0.2s;\n    transition: color 0.2s;\n    border-radius: 5px;\n\n    &.active,\n    &:hover {\n      color: white;\n    }\n\n    &.active {\n      border: 1px solid $green;\n\n      &,\n      &:hover {\n        background-color: #2de25e;\n        color: white;\n      }\n    }\n\n    &:hover {\n      background-color: #72f195;\n      text-decoration: none;\n    }\n  }\n}\n",".hero {\n  padding: 35px 0;\n\n  &--smaller {\n    padding: 10px 0;\n  }\n\n  &__heading,\n  &__search-form {\n    margin-left: auto;\n    margin-right: auto;\n    max-width: 800px;\n    width: 90%;\n  }\n\n  &__heading {\n    color: white;\n    margin-top: 0;\n    text-align: center;\n    margin-bottom: 10px;\n  }\n}",".image-container{\n  position: relative;\n  padding-top: 70%;\n  overflow: hidden;\n\n  &--lg{\n    @media (max-width: 991px){\n      padding-top: 80%;\n    }\n  }\n}\n",".image-fill{\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  margin: 0 auto;\n\n  &--natural{\n    width: auto;\n    height: auto;\n    top: 50%;\n    transform: translateY(-50%);\n  }\n\n  &--natural-xs{\n    @media (max-width: 500px){\n      width: auto;\n      height: auto;\n      top: 50%;\n      transform: translateY(-50%);\n    }\n  }\n}\n",".locations{\n  position: relative;\n\n  &__select{\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    padding: 15px;\n    cursor: pointer;\n    width: 100%;\n    border: 0;\n    border-radius: 0;\n    font-size: $input-font-size;\n    @include input-font;\n  }\n\n  &__drop-down-arrow{\n    width: 10px;\n    position: absolute;\n    right: 15px;\n    top: 50%;\n    transform: translateY(-50%);\n    pointer-events: none;\n  }\n}\n","@mixin input-font{\n  color: #5f5f5f;\n  font-weight: 200;\n}\n",".logo-bar{\n  padding-top: 10px;\n  text-align: center;\n\n  &__picture{\n    display: inline-block;\n  }\n\n  &__logo{\n    width: $logo-width;\n  }\n}\n",".recommended {\n  @media (max-width: 767px){\n    padding-right: 15px;\n  }\n\n  &__deal-container{\n    margin-bottom: 20px;\n  }\n\n  &__label{\n    margin-bottom: 10px;\n    font-size: 14px;\n  }\n\n  &__deal{\n    display: flex;\n    border: 1px solid white;\n    transition: border-color 0.2s;\n    cursor: pointer;\n\n    &:hover {\n      border-color: $border-colour;\n    }\n  }\n\n  &__primary {\n    width: 40%;\n    padding-right: 10px;\n  }\n\n  &__secondary {\n    width: 60%;\n    padding: 5px 0;\n  }\n\n  &__deal-title {\n    font-size: 13px;\n    font-weight: 300;\n    max-height: 45px;\n    overflow-y: hidden;\n    position: relative;\n    top: 50%;\n    transform: translateY(-50%);\n  }\n}\n",".related{\n\n  &__link{\n    color: $font-colour-primary;\n    font-size: 14px;\n    font-weight: 300;\n    padding-left: 10px;\n    position: relative;\n    display: block;\n\n    &:hover{\n      text-decoration: underline;\n    }\n\n    &:before{\n      content: '>';\n      position: absolute;\n      left: 0;\n      top: 50%;\n      transform: translateY(-50%);\n    }\n  }\n\n  &__link-container{\n    margin-bottom: 3px;\n  }\n}\n",".result {\n  display: flex;\n  @media (max-width: 500px){\n    flex-direction: column;\n    border: 1px solid $border-colour;\n  }\n\n  &__primary,\n  &__secondary {\n    padding-right: 15px;\n    @media (max-width: 500px) {\n      margin-bottom: 10px;\n      padding: 0;\n    }\n  }\n\n  &__primary {\n    width: 20%;\n    @media (max-width: 500px) {\n      width: 100%;\n    }\n  }\n\n  &__secondary {\n    width: 60%;\n    @media (max-width: 500px) {\n      width: 100%;\n      padding: 0 15px;\n    }\n  }\n\n  &__tertiary {\n    width: 20%;\n    @media (max-width: 500px) {\n      width: 100%;\n      padding: 0 15px 15px;\n    }\n  }\n\n  &__title {\n    font-weight: 600;\n    margin-bottom: 10px;\n    color: $font-colour-primary;\n    @media (max-width: 500px){\n      height: 75px;\n      display: block;\n      overflow-y: hidden;\n    }\n  }\n\n  &__description {\n    font-size: 14px;\n    font-weight: 300;\n    line-height: 1.3;\n    height: 55px;\n    overflow-y: hidden;\n    display: none;\n  }\n\n  &__price,\n  &__rating {\n    margin-bottom: 5px;\n  }\n\n  &__link {\n    color: $green;\n    display: block;\n\n    &:hover {\n      text-decoration: underline;\n    }\n  }\n\n  &__topratedlisting {\n    visibility: hidden;\n    font-size: 9px;\n    background: $green;\n    color: white;\n    padding: 3px;\n    display: inline-block;\n    text-transform: uppercase;\n    margin-bottom: 8px;\n\n    &--visible{\n      visibility: visible;\n    }\n  }\n}\n",".search-form-small{\n  display: flex;\n\n  &__search-field{\n    padding: 10px;\n    width: 100%;\n    max-width: 360px;\n    border-left: 1px solid $green;\n    border-top: 1px solid $green;\n    border-bottom: 1px solid $green;\n    border-